import { Component, OnInit } from '@angular/core';
import { navList } from './navList'

@Component({
  selector: 'app-left-bar',
  templateUrl: './left-bar.component.html',
  styleUrls: ['./left-bar.component.less']
})
export class LeftBarComponent implements OnInit {
  navList = navList;
  constructor() { }

  ngOnInit(): void {
  }

  clickFn($event) {
    let allList: any = document.getElementsByClassName('list-item');
    // console.log(allList);
    //获取当前点击dom节点

    let thisList: any = event.target;
    // console.log(thisList);

    // 获取当先节点下的dd
    let thisListItem: any = thisList.nextSibling.getElementsByTagName('dd');
    // console.log(thisListItem);

    // 排他 全部收起
    for (var i = 0; i < allList.length; i++) {
      allList[i].style.height = '0px';
    }
    // 展开
    thisList.nextSibling.style.height = 40 * thisListItem.length + "px";
  }
}
