import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http'
import { ActivatedRoute, Router, NavigationEnd, } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class ArticleService {

  constructor(
    private http: HttpClient,
    private router: Router,
    private route: ActivatedRoute
  ) { }


  public ARTList: any;
  public oldArticleData: any;
  // 请求头
  private httpOptions = {
    headers: new HttpHeaders({ 'content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' })
  }

  onInit() {
  }


  //路由带参数跳转
  getSearch(params) {
    this.router.navigate(['/article'], {
      queryParams: params
    });
  }

  /* ======== 获取列表  按条件获得article列表 =============*/
  getARTList() {
    // 监听路由变化
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        // 获取参数
        this.route.queryParams.subscribe(res => {
          // 发送请求
          this.toGetList(res)
          console.log(this.ARTList)
        });
      }
    });
  }


  // 发送get 请求
  toGetList(data) {
    let a = this
    // console.log(a)
    return new Promise(function (resolve, reject) {
      a.http.get('api/a/article/search', { params: data })
        .subscribe(
          (res: any) => {
            a.ARTList = res.data;
            console.log(a.ARTList);
            resolve('请求成功');
          })
    })
  }


  /* ============   新增Article   ========== */
  addArticle(data) {

    // 数据格式转换
    let postData: string = '';
    for (let key in data) {

      postData += key + '=' + data[key] + '&';
    }
    console.log(data)
    console.log(postData)
    return this.http.post('api/a/u/article', postData, this.httpOptions)
  }

  /* ============ 修改article的上架/下架（status）========= */
  changeStatus(ID, Status) {

    let data = 'id=' + ID + '&status=' + Status;
    console.log(data);
    return this.http.put('api/a/u/article/status', data, this.httpOptions)
  }
  /* 删除 article */
  delArticle(id) {
    return this.http.delete('api/a/u/article/' + id)
  }

  // ========= 编辑article ============

  // 获取单个文章信息
  getThisArticle(id) {
    // 获取文章数据并保存
    return this.http.get('api/a/article/' + id)
  }

  editArticle() {
    // 数据转换
    let putData: string = '';
    for (let key in this.oldArticleData) {
      putData += key + '=' + this.oldArticleData[key] + '&';
    }

    return this.http.put('api/a/u/article/' + this.oldArticleData.id, putData, this.httpOptions)

  }
}
