import { Injectable, Inject } from "@angular/core";


import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router, NavigationStart } from "@angular/router";

@Injectable()
export class LoginGuard implements CanActivate {

    constructor(private router: Router,) {
    }
    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
        let isLogin: boolean = false;
        // 读取cookie
        let cookies = document.cookie.split(";");
        console.log(cookies)
        let isCookie: object = {};
        if (cookies.length > 0) {
            for (let cookie of cookies) {
                if (cookie.indexOf('www.zonzii.com=') > -1) {
                    isLogin = true;
                }
            }
        }
        //             /* 该cookie格式
        //              *www.zonzii.com={id}|{data}
        //              */
        //             let value = cookie.split('=')[1];
        //             isCookie.id = value.split("|")[0];
        //             isCookie.data = value.split("|")[1];
        //         }
        //     }
        // }
        // if (isCookie.length) {
        //     isLogin = true;
        //     alert('true')
        //
        if (!isLogin) {
            this.router.navigateByUrl('login');
        }
        return isLogin;

    }
}