  // 日期选择器 变化
  dateChange(result: Date): void {
    console.log(result);
    if (result[0]) {
      let a = new Date(result[0])
      let b = new Date(result[1])
      // 将date对象转化为时间戳
      this.filter.startAt = a.getTime();
      this.filter.endAt = b.getTime();
    } else {
      this.filter.startAt = '';
      this.filter.endAt = '';
    }
  }
  // 限制选择今天之后的日期
  disabledDate = (current: Date): boolean => {
    let today = new Date();
    return differenceInCalendarDays(current, today) > 0;
  };

  // 生成查询参数
  creatParams() {
    // 浅拷贝
    var filter: any = Object.assign({}, this.filter);
    // 遍历查询参数 去除空值属性
    for (let key in filter) {
      if (filter[key] == "") {
        delete filter[key];
      }
    }
    return filter;
  }

  // 查询
  articleSearch() {

    let params = this.creatParams();
    // 调用服务方法 参数跳转
    this.articleService.getSearch(params);
  }

  //清空
  clearfilter() {
    // 初始化
    this.filter = {
      'title': '',
      'author': '',
      'startAt': '',
      'endAt': '',
      'type': "",
      'status': "",
    };
    this.dateRange = undefined;
    // 查询
    this.articleSearch();
  }

  // 上下线article
  changeStatus(id, status) {
    this.articleService.changeStatus(id, status)
      .subscribe(
        (res: any) => {
          if (res.code == 0) {
            this.success();
          } else {
            this.error();
          }
        }
      )
  }


