import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpRequest, HttpEventType, } from '@angular/common/http';

import { Router } from '@angular/router'
import { ArticleService } from '../../service/article.service'
import { NzModalService } from 'ng-zorro-antd/modal';

import { config } from './quill.config';

@Component({
  selector: 'app-article-detail',
  templateUrl: './article-detail.component.html',
  styleUrls: ['./article-detail.component.less']
})

export class ArticleDetailComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private articleService: ArticleService,
    private modalService: NzModalService,
    private router: Router
  ) { }


  // quill配置参数
  public quillConfig = config;
  // 
  public editInfo: any = {
    'title': '',
    'type': '0',
    'status': '',
    "img": '',
    'url': 'sa',
    'content': '',
    'industry': '0',
  }
  public imgMsg: any = {
    'name': '',
    "size": '',
    'img': '',
    'status': '',
  }
  // 富文本内容
  public quillContent: object;

  // 上传进度条
  public ProgressPercent = 0;


  // 找出对象A、B相同的属性，并B中的属性值赋值给A
  replaceValue(ObjA, ObjB) {
    for (let keyA in ObjA) {
      for (let keyB in ObjB) {
        if (keyA == keyB) {
          ObjA[keyA] = ObjB[keyB] + ''
        }
      }
    }
  }

  ngOnInit(): void {
    if (this.articleService.oldArticleData) {
      // 遍历对象 将表单中属性对应的值替换为service中对应的属性值
      this.replaceValue(this.editInfo, this.articleService.oldArticleData);
    }
  }
  ngAfterViewChecked() {
    let btn: any = document.querySelectorAll('.btn-success');
    // 表单检测
    if (this.checkForm()) {
      // 禁用按键
      btn[0].disabled = false;
      btn[0].style.cursor = "pointer";
      btn[1].disabled = false;
      btn[1].style.cursor = "pointer";
    } else {
      btn[0].disabled = true;
      btn[0].style.cursor = "no-drop";
      btn[1].disabled = true;
      btn[1].style.cursor = "no-drop";
    }
  }

  // change事件获取图片数据
  getMsg() {
    // 获取file对象
    let file: any = document.querySelector('#img')
    let athis = this;
    //创建FileReader对象
    var reader = new FileReader();
    //将数据转化为base64格式  
    if (file.files[0]) {
      reader.readAsDataURL(file.files[0]);
    }
    reader.onload = function () {
      //同步图片src  图片预览
      athis.imgMsg.img = reader.result;
    }


    // 本地对象赋值
    this.imgMsg.name = file.files[0].name;
    this.imgMsg.size = (file.files[0].size / (1024 * 1024)).toFixed(2) + ' Mb'

    // 清空数据
    this.ProgressPercent = 0;
    this.editInfo.img = '';
    this.imgMsg.status = '';

  }


  // 上传
  upLoad() {


    // 获取文件对象
    let file = (<HTMLInputElement>document.querySelector('#img')).files[0];

    console.log(/image/.test(file.type));

    // 限制上传图片大小和类型
    if (file.size > 1500000 || !/image/.test(file.type)) {
      console.log(/image/.test(file.type))
      this.subError('请确认图片格式且大小不超过1.5MB', false);
    } else {

      // 加载状态
      this.imgMsg.status = 3;

      // 创建对象
      const formData = new FormData();
      // 添加请求数据
      formData.append("file", file);
      // 请求方式
      const req = new HttpRequest('POST', `api/a/u/img/task`, formData, {
        //该请求是否应该暴露出进度事件
        reportProgress: true,
      });

      // 返回一个HttpEvents 的 Observable
      this.http.request(req).subscribe(
        (event: any) => {

          // 收到了上传进度事件。
          if (event.type === HttpEventType.UploadProgress) {
            console.log(event);
            // 进度条变化
            // var elem = <HTMLInputElement>document.querySelector("#Progress");
            // elem.style.width = (event.loaded / event.total) * 50 + 'px';
            this.ProgressPercent = (event.loaded / event.total) * 100;
            // 获取响应内容
          } else if (event.type == HttpEventType.Response) {

            if (event.status == 200) {
              // 获取图片url地址
              this.editInfo.img = event.body.data.url
              this.imgMsg.status = 1;
            } else {
              this.imgMsg.status = 0;
            }

          }
        }
      );
    }

  }


  // 删除
  clearMsg() {
    let file: any = (<HTMLInputElement>document.querySelector('#img'))
    // 清空数据
    file.value = '';

    this.imgMsg = {
      'name': '',
      "size": '',
      'url': '',
      'status': '',
    }
    this.editInfo.img = '';
    this.ProgressPercent = 0;
  }

  // 表单验证
  checkForm() {
    let flag: boolean = true;
    let data: any = this.editInfo;

    if (!data.title || !data.type || !data.url || !data.img) {
      flag = false;
    }
    if (flag) {
      if (data.type == 3 && data.industry == '') {
        flag = false;

      }
    }
    return flag;
  }

  // 上线 存草稿 
  submitArticle(status) {
    this.editInfo.status = status;

    // 新增article
    if (!this.articleService.oldArticleData) {
      // post请求
      this.articleService.addArticle(this.editInfo)
        .subscribe((res: any) => {
          console.log(res)
          if (res.code == 0) {
            this.subSuccess();
          } else {
            this.subError('请重试', true);
          }
        })
    }
    // 修改article 
    else {
      // 合并数据 
      this.replaceValue(this.articleService.oldArticleData, this.editInfo)

      // put请求
      this.articleService.editArticle()
        .subscribe(
          (res: any) => {
            console.log(res)
            if (res.code == 0) {
              this.subSuccess();
            } else {
              this.subError('请重试', true);
            }
          }
        )
    }
  }
  // 弹窗
  subSuccess(): void {
    const modal = this.modalService.success({
      nzTitle: '操作成功',
      nzContent: '页面将在2s后跳转',
    })
    modal.afterClose.subscribe(() => this.subSuccessBack(modal));
    setTimeout(() => modal.destroy(), 2000);
  }

  // 页面跳转
  subSuccessBack(modal) {
    this.router.navigate(['/article']);
    modal.destroy();
  }

  //错误提示 
  subError(content, boolean): void {
    const modal = this.modalService.error({
      nzTitle: '操作失败',
      nzContent: content,
    })
    if (boolean) {
      setTimeout(() => modal.destroy(), 2000);
    }

  }



}